import React, {useState, useEffect} from 'react'
import {Link} from "react-router-dom"

import Table_Pupils from './../../student/table/table.js'

// Style
import styled from '@emotion/styled'

import Url from './../../../../url/url.js'
import localStorage_change from '../../../../#localStorage_change.js'
import ReturnAge from './../../return-age/returnAge.js' // Определяет возраст
import Pagination from './../../#more-functions/pagination/pagination.js'

const Create_Columns_Rows_Pupils = () => {

	const [offsetG, setOffsetG] = useState(0)
	const [Count, setCount] = useState(1)

	const axios = require('axios'); // AJAX

	const [objPupils, setObjPupils] = useState([])

	let dataSource = objPupils.map(item => {

		let {
		    _id,
		    surname,
		    name,
		    midname,
		    age,
		    gender,
		    phone,
		    balance,
		    parentPhone,
		    discord,
		    groups,
		} = item

		return ({
			key: _id,
			surname: () => (
				<Link
					className="link-info"
					to={`/student/${_id}`}>

					{surname}
				</Link>
				),
			name: name,
			midname: midname,
			age: ReturnAge(age),
			gender: gender,
			phoneNumber: phone,
			tutor: '',
			parentPhoneNumber: parentPhone,
			balance: `${balance}`,
			groups: groups,
			discordNickname: discord
		})
	})

	// Filtered
	const [paramFilrers, setParamFilrers] = useState({
	    surname: [],
	    name: [],
	    midname: [],
	    age: [],
	    gender: [],
	    groups: [],
	    balance: [], // {
	    // 	$gt: 0, // Больше
	    // 	// $lte: 0 // Меньше или равно
	    // 	$lt: 0 // Меньше
	    // }
	})
	const [dataGroupsCheckedG, setDataGroupsCheckedG] = useState({})

	const [globalFilters, setGlobalFilters] = useState({
	    surname: [],
	    name: [],
	    midname: [],
	    age: [],
	})

	const [loadingButtomFilrer, setLoadingButtomFilrer] = useState(false)

	const [filterDropdownVisible, setFilterDropdownVisible] = useState({
	    surname: false,
	    name: false,
	    midname: false,
	    age: false,
	})
	// /Filtered

	const update = (offset = 0, param = {}, filtersGroups = {}) => {
	    setLoadingButtomFilrer(true);

	    let filtersLocal = {};

	    for (let key in filtersGroups) {
	        if (filtersLocal.groups) {
	            filtersLocal.groups.push(key);
	        } else {
	            filtersLocal.groups = [];
	            filtersLocal.groups.push(key);
	        }
	        // filtersLocal.groups = [...filtersLocal.groups]
	    }

	    for (let key in param) {
	        if (param[key][0]) {
	            if (
	                key == "surname" ||
	                key == "name" ||
	                key == "midname" ||
	                key == "age"
	            ) {
	                filtersLocal[`${key}s`] = param[key];
	            } else {
	                filtersLocal[`${key}`] = param[key];
	            }
	        }
	    }

	    if (filtersLocal.ages) {
	        let age = [...filtersLocal.ages];
	        filtersLocal.ages = [];
	        age.forEach((age) => {
	            filtersLocal.ages.push(+age);
	        });
	    }

	    if (filtersLocal.balance) {
	        let balance = [...filtersLocal.balance];
	        if ((balance[0] == 1) & !balance[1]) {
	            filtersLocal.balance = { $gte: 0 };
	        } else if ((balance[0] == 2) & !balance[1]) {
	            filtersLocal.balance = { $lt: 0 };
	        } else if (balance[1]) {
	            filtersLocal.balance = { $gte: 0, $lte: 0 };
	        }
	    }

	    let p = {
	        filters: filtersLocal,
	    };

	    axios({
	        method: "post",
	        url: `${Url}/CRM/Pupils/find?limit=10&offset=${offset}`,
	        headers: {
	            "Content-Type": "application/json;charset=utf-8",
	            Authorization: `Bearer ${localStorage.getItem("tokenID")}`,
	        },
	        data: p,
	    })
	        .then((res) => {
	            let { data, headers } = res;
	            setObjPupils(data);
	            setCount(headers.count);

	            setFilterDropdownVisible({
	                surname: false,
	                name: false,
	                midname: false,
	            });
	            setLoadingButtomFilrer(false);
	        })
	        .catch((error) => {
	            if (error.response) {
	                setLoadingButtomFilrer(false);
	                let { status, data } = error.response;
	                if (status == 401) {
	                    if (data.message == "TOKEN_EXPIRED") {
	                        localStorage_change(data.token);
	                        update();
	                    } else {
	                        localStorage.removeItem("tokenID");
	                        window.location.replace("/");
	                    }
	                } else if (status == 500) {
	                    // swallErr("Ошибка сервера");
	                }
	            }
	        });
	};

	useEffect(() => {
	    update();
	}, [])

	const breakpoints = [1020, 700],
	mq = breakpoints.map(
		bp => `@media (max-width: ${bp}px)`
		)

	const TableL = styled.div({
		padding: '0 30px',
		[mq[1]]: {
			padding: '0 0',
		},
		'.log_h2': {

		},
		td: {
			paddingTop: '1px',
			paddingBottom: '4px'
		},
		'th, td': {
			whiteSpace: 'nowrap'
		}
	})

	return (
		<TableL>
			<Table_Pupils
				scroll={{ x: 1000 }}
			    paramFilrersG={paramFilrers}
			    setParamFilrersG={setParamFilrers}
			    globalFiltersG={globalFilters}
			    setGlobalFiltersG={setGlobalFilters}
			    dataGroupsCheckedG={dataGroupsCheckedG}
			    setDataGroupsCheckedG={setDataGroupsCheckedG}

			    dataSource={dataSource}
			    update={update}

			    loadingButtomFilrer={loadingButtomFilrer}
			    setLoadingButtomFilrer={setLoadingButtomFilrer}
			    filterDropdownVisible={filterDropdownVisible}
			    setFilterDropdownVisible={setFilterDropdownVisible}
			/>
			<Pagination getItem={update} count={Count} offset={offsetG} setOffset={setOffsetG}/>
		</TableL>
	)
}

export default Create_Columns_Rows_Pupils